.atr_picture-bg {
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  z-index: -1;
}
.atr_picture-bg img {
  height: auto;
  width: 100%;
  object-fit: cover;
}

.atr_button {
  background-color: #d3e3f3;
  border: none;
  border-radius: 2px;
  color: #255891;
  font-size: 14px;
  font-weight: 800;
  letter-spacing: 2px;
  margin-top: 40px;
  padding: 6px 40px;
  text-align: center;
  transition: background-color 0.2s ease;
}
.atr_button:hover {
  background-color: #f5f5f5;
}

.atr_h2 {
  color: #f5f5f5;
  font-weight: 300;
  letter-spacing: 1px;
  margin: 0;
  text-align: center;
  font-size: 80px;
  line-height: 1.1;
}
.atr_h2 span {
  display: block;
}
.atr_h2 .atr_h-small {
  color: #f5f5f5;
  font-weight: 300;
  letter-spacing: 1px;
  margin: 0;
  text-align: center;
  font-size: 48px;
  line-height: 1.5;
}

.atr_offleft {
  position: relative;
  right: 120px;
}

@media only screen and (max-width: 992px) {
  .atr_h2 {
    font-size: 70px;
  }
  .atr_h2 .atr_h-small {
    font-size: 42px;
  }

  .atr_offleft {
    right: 100px;
  }

  .atr_button {
    font-size: 12px;
    padding: 6px 30px;
  }
}
@media only screen and (max-width: 768px) {
  .atr_h2 {
    font-size: 60px;
  }
  .atr_h2 .atr_h-small {
    font-size: 36px;
  }

  .atr_offleft {
    right: 80px;
  }
}
@media only screen and (max-width: 576px) {
  .atr_h2 {
    font-size: 42px;
  }
  .atr_h2 .atr_h-small {
    font-size: 24px;
  }

  .atr_offleft {
    right: 60px;
  }
}
.atr_why-lt-container .atr_blue-block {
  background: linear-gradient(50deg, #7a7a86 0%, #6a768e 100%);
  margin: 0 auto;
  height: 160px;
  width: 80vw;
}
.atr_why-lt-container section {
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-flex-direction: column;
      -ms-flex-direction: column;
          flex-direction: column;
  height: 90vh;
  overflow: hidden;
  position: relative;
}
.atr_why-lt-container section h2 {
  margin: 60px 50px 0 50px;
}
.atr_why-lt-container section h2 .atr_align-right {
  text-align: right;
  margin-right: -50px;
}

@media only screen and (max-width: 992px) {
  .atr_why-lt-container .atr_blue-block {
    height: 120px;
  }
  .atr_why-lt-container section h2 {
    margin-top: 40px;
  }
  .atr_why-lt-container section h2 .atr_align-right {
    margin-right: 0;
  }
}
@media only screen and (max-width: 768px) {
  .atr_why-lt-container .atr_blue-block {
    height: 80px;
  }
  .atr_why-lt-container section {
    margin-bottom: 9vh;
  }
  .atr_why-lt-container section h2 {
    margin: 30px 30px 0 30px;
  }
}
@media only screen and (max-width: 576px) {
  .atr_why-lt-container section h2 {
    margin: 20px 20px 0 20px;
  }
}